/* Imports
----------------------------------------------------------------------*/
@import 'angulara-defaults';
@import 'custom-data-table';

/* Variables
----------------------------------------------------------------------*/
$grayColor: #444444;
$footerBgColor: #f5f5f5;

/* Mixins
----------------------------------------------------------------------*/
@mixin borderRadius($radius) {
-webkit-border-radius: $radius;
    -moz-border-radius: $radius;
    -ms-border-radius: $radius;
    -o-border-radius: $radius;
        border-radius: $radius;
}

@mixin spinAnimation() {
-webkit-animation: fa-spin 2s infinite linear;
    animation: fa-spin 2s infinite linear;
}

@mixin lwTransition($time:0.3s, $item:all) {
        -webkit-transition  : $item $time ease-in-out;
    -moz-transition     : $item $time ease-in-out;
    -ms-transition      : $item $time ease-in-out;
    -o-transition       : $item $time ease-in-out;
        transition         : $item $time ease-in-out;
}

