    SIDEBAR STYLE
----------------------------------------------------- */

	a,
a:hover,
a:focus {
    // color: inherit;
    text-decoration: none;
    transition: all 0.3s;
}

.navbar {
    background: #fff;
    border: none;
    border-radius: 0;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}

