    box-shadow: none;
    outline: none !important;
    border: none;
}

.lw-main-wrapper {
    display: flex;
    align-items: stretch;
}

.lw-sidebar {
    min-width: 250px;
    max-width: 250px;
    background: #565656;
    color: #fff;
    transition: all 0.3s;
    z-index: 999;
    height:100vh;
    left: 0;
    top: 0;
    position: fixed;
    overflow: auto;
    padding-bottom: 60px;

    .lw-sidebar-item-label  {
		margin-left:10px;
	}

    .dropdown-toggle::after {
	    display: block;
	    position: absolute;
	    top: 50%;
	    right: 20px;
	    transform: translateY(-50%);
	}

	/* ------- when active --------- */
	&.lw-sidebar-collapsed {
	    min-width: 60px;
	    max-width: 60px;
	    text-align: center;
	    
	    .lw-sidebar-header {
		    padding: 20px 0px;
		    h3 {
		    	display: none;
		    }
		}

	    .lw-sidebar-header strong {
		    display: block;
		}

		ul li a {
		    // padding: 15px 10px;
		    font-size: 0.85em;

		    i {
			    margin-right: 0;
			    font-size: 1.3em;
			    margin-bottom: 5px;
			}
		}

		 
		.dropdown-toggle::after {
		    -webkit-transform: translateX(50%);
		    -ms-transform: translateX(50%);
		    transform: translateX(50%);
		}

		.lw-sidebar-item-label  {
			display:none;
		}

        .lw-sidebar-hover-link:hover {
            position: relative;
            width: 240px;
            z-index: 999;
            text-align: left;
            transition: all 0.3s;

            .lw-sidebar-item-label  {
                display:unset;
            }
        }

		.dropdown-toggle:hover,.dropdown-item:hover,
        .nav-link:hover {

			position: relative;
			width: 240px;
			z-index: 999;
			text-align: left;
			transition: all 0.3s;
			// padding-left: 30px;

			.lw-sidebar-item-label {
				display:none;
			}

			.dropdown-item {
			    position: relative;
				width: 200px;
				z-index: 999;
				text-align: left;
				transition: all 0.3s;
				padding-left: 30px;
			}

		}
	}

	/* ------ end active --------- */


	ul li a {
	    text-align: left;
	}

	.lw-sidebar-header {
	    padding: 20px;
	    background: #565656;
	    border-bottom: 1px solid #56565f;

	    strong {
		    display: none;
		    font-size: 1.8em;
		}
	}

	ul.lw-sidebar-components {
	    padding: 20px 0;
	    // border-bottom: 1px solid #47748b;
	}

	ul li a {
	    padding: 10px;
	    display: block;
	    border-radius: 0;
	}

	ul li a:hover {
		color: #fff;
		background: #868686;
	}
 
	ul li.active>a, a[aria-expanded="true"] {
	    color: #fff;
	    background: #565656;
	}
	.dropdown-item {
		padding : 15px 1.5rem;
		border-radius:0;
		background-color: #686868;
		color:#fff;
	}

	.dropdown-item.active, .dropdown-item:active {
		background-color: #868686;
	}
}
 
