div.dataTables_wrapper {
    .dt-buttons.btn-group {
        margin-bottom: -45px;
         @media (max-width: 960px) {
            margin-bottom: 10px;
        }
    }

   .dt-buttons ~ div.dataTables_paginate {
        margin-top: -36px;
        @media (max-width: 960px) {
            margin-top: 0;
        }
    }

th, td { white-space: nowrap; }

        width: 100%;
        margin: 0 auto;
}

 a.btn {
    margin-bottom: 5px;
}

table.dataTable thead th, table.dataTable thead td {
  padding: 16px 18px;
//   border-bottom: 1px solid #C3C2C2;

    border: none;
    // border-left: 1px solid #C3C2C2;
    background: #f7f7f7;
}

/* table.dataTable.display tbody td {
  border-top: 1px solid #C3C2C2;
} */
.scrollX {
  overflow: auto; width: 100%;
}

 .dataTables_wrapper.no-footer .dataTables_scrollBody {
border-bottom: 1px solid #C3C2C2;
} 

table.dataTable.no-footer {
  border-bottom: 1px solid #C3C2C2;
}
.data-table-loader{
  position:absolute;
  margin-top:70px;
  font-size:150%;
}

tbody tr.odd, table.dataTable.display tbody tr.odd {
	// background-color: #F0F0F0;
    background-color: #FFF;
}

table.dataTable.display tbody tr.even>.sorting_1, table.dataTable.order-column.stripe tbody tr.even>.sorting_1 {
	background-color: #BAB7B7;
}

.table-striped>tbody>tr:nth-of-type(odd), table.dataTable>tbody>tr:nth-of-type(odd) {
  background-color: #FFF;
}

table.dataTable > tbody > tr.child {

    span.dtr-title {
        min-width: 120px;
    }
   
    span.dtr-data, span.dtr-title {
        
        white-space: normal;

        @media (max-width: 560px) {
            width: 100%;
            min-width: 20px;
        }
    } 
}

table.dataTable > {
    tbody , tfoot, tfoot, thead  {
        >tr>td/* , >tr>th */ {
            white-space: normal;
        }
    }
}
// Responsive datatable expand/collabs button
table.dataTable.dtr-inline.collapsed>tbody>tr>td:first-child:before, 
table.dataTable.dtr-inline.collapsed>tbody>tr>th:first-child:before {
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    display: block;
    position: absolute;
    color: #fff;
    border: none;
    border-radius: 0;
    box-shadow: none;
    box-sizing: content-box;
    text-align: center;
    font-family: 'Courier New', Courier, monospace;
    line-height: 14px;
    content: '+';
    background-color: #337ab7;
    padding-top: 5px;
    opacity: 0.8;
    border-bottom-right-radius: 4px;
}

table.dataTable.dtr-inline.collapsed > tbody > tr[role="row"] > td:first-child:before, table.dataTable.dtr-inline.collapsed > tbody > tr[role="row"] > th:first-child:before {
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    display: block;
    position: absolute;
    color: #fff;
    border: none;
    border-radius: 0;
    box-shadow: none;
    box-sizing: content-box;
    text-align: center;
    font-family: 'Courier New', Courier, monospace;
    line-height: 14px;
    content: '+';
    background-color: #337ab7;
    padding-top: 5px;
    opacity: 0.8;
    border-bottom-right-radius: 4px;
}